import React from "react";
import { Link } from "react-router-dom"; // Usamos Link de react-router-dom para la navegación
import { LogOut, User, Users, Edit, Brain } from "lucide-react"; // Importamos iconos
import { Card, CardContent } from "../components/ui/card"; // Componentes de tarjeta
import { Button } from "../components/ui/button2"; // Botón de cierre de sesión
import { useNavigate } from "react-router-dom"; // Importamos useNavigate para redirigir

export default function AdminDashboard() {
  const navigate = useNavigate(); // Usamos el hook de redirección

  // Función para manejar el cierre de sesión
  const handleLogout = () => {
    // Elimina el token o cualquier otro dato relevante
    localStorage.removeItem("token"); // Aquí puedes usar sessionStorage si es necesario

    // Redirige al usuario a la página de inicio de sesión
    navigate("/login"); // Cambia '/login' por la ruta que corresponda
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-indigo-50 via-blue-50 to-cyan-50 relative overflow-hidden">
      {/* Fondo animado */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-20 left-10 w-32 h-32 bg-blue-200/20 rounded-full blur-xl animate-pulse"></div>
        <div className="absolute bottom-20 right-10 w-40 h-40 bg-purple-200/20 rounded-full blur-xl animate-pulse delay-1000"></div>
        <div className="absolute top-1/2 left-1/4 w-24 h-24 bg-green-200/20 rounded-full blur-xl animate-pulse delay-500"></div>
        <div className="absolute top-1/3 right-1/3 w-28 h-28 bg-yellow-200/20 rounded-full blur-xl animate-pulse delay-700"></div>
      </div>

      {/* Header */}
      <div className="bg-white/90 backdrop-blur-md border-b border-blue-100 shadow-lg relative z-10">
        <div className="max-w-6xl mx-auto px-6 py-6">
          <div className="flex items-center justify-center">
            <div className="relative">
              <img
                src={`${process.env.PUBLIC_URL}/logotipo2.png`} // Usamos imagen desde la carpeta public
                alt="Emotion AI Logo"
                width={70}
                height={70}
                className="mr-4 drop-shadow-lg"
              />
              <div className="absolute -top-1 -right-1 w-4 h-4 bg-green-400 rounded-full animate-ping"></div>
            </div>
            <div className="text-center">
              <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 via-purple-600 to-teal-600 bg-clip-text text-transparent">
                Admin Dashboard
              </h1>
              <p className="text-gray-600 text-sm mt-1">Administración de usuarios</p>
            </div>
          </div>
        </div>
      </div>

      {/* Contenido principal */}
      <div className="max-w-6xl mx-auto px-6 py-12 relative z-10">
        <div className="grid md:grid-cols-3 gap-8 mb-12">
          {/* Tarjeta Perfil */}
          <Link to="/Admin/perfil">
            <Card className="group hover:shadow-2xl transition-all duration-500 transform hover:-translate-y-3 bg-gradient-to-br from-blue-500 via-blue-600 to-indigo-600 border-0 cursor-pointer overflow-hidden relative">
              <div className="absolute inset-0 bg-gradient-to-r from-blue-400/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
              <CardContent className="p-10 text-center text-white relative z-10">
                <div className="bg-white/20 rounded-full w-24 h-24 flex items-center justify-center mx-auto mb-8 group-hover:bg-white/30 transition-all duration-300 group-hover:scale-110">
                  <User className="w-12 h-12" />
                </div>
                <h3 className="text-3xl font-bold mb-4">Mi Perfil</h3>
                <p className="text-blue-100 text-lg leading-relaxed mb-6">
                  Visualiza y edita tu perfil
                </p>
                <div className="inline-flex items-center text-white/90 group-hover:text-white transition-colors bg-white/10 px-6 py-3 rounded-full group-hover:bg-white/20">
                  <span className="mr-3 font-medium">Ver perfil</span>
                  <svg
                    className="w-5 h-5 transform group-hover:translate-x-2 transition-transform duration-300"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </div>
              </CardContent>
            </Card>
          </Link>

          {/* Tarjeta Registrar Usuarios */}
          <Link to="/Admin/registro-usuario">
            <Card className="group hover:shadow-2xl transition-all duration-500 transform hover:-translate-y-3 bg-gradient-to-br from-purple-500 via-pink-500 to-rose-500 border-0 cursor-pointer overflow-hidden relative">
              <div className="absolute inset-0 bg-gradient-to-r from-pink-400/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
              <CardContent className="p-10 text-center text-white relative z-10">
                <div className="bg-white/20 rounded-full w-24 h-24 flex items-center justify-center mx-auto mb-8 group-hover:bg-white/30 transition-all duration-300 group-hover:scale-110">
                  <Edit className="w-12 h-12" />
                </div>
                <h3 className="text-3xl font-bold mb-4">Registrar Usuario</h3>
                <p className="text-pink-100 text-lg leading-relaxed mb-6">
                  Añade nuevos usuarios al sistema
                </p>
                <div className="inline-flex items-center text-white/90 group-hover:text-white transition-colors bg-white/10 px-6 py-3 rounded-full group-hover:bg-white/20">
                  <span className="mr-3 font-medium">Registrar usuario</span>
                  <svg
                    className="w-5 h-5 transform group-hover:translate-x-2 transition-transform duration-300"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </div>
              </CardContent>
            </Card>
          </Link>

          {/* Tarjeta Gestionar Usuarios */}
          <Link to="/Admin/gestionar-usuarios">
            <Card className="group hover:shadow-2xl transition-all duration-500 transform hover:-translate-y-3 bg-gradient-to-br from-green-500 via-teal-500 to-blue-500 border-0 cursor-pointer overflow-hidden relative">
              <div className="absolute inset-0 bg-gradient-to-r from-teal-400/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>
              <CardContent className="p-10 text-center text-white relative z-10">
                <div className="bg-white/20 rounded-full w-24 h-24 flex items-center justify-center mx-auto mb-8 group-hover:bg-white/30 transition-all duration-300 group-hover:scale-110">
                  <Users className="w-12 h-12" />
                </div>
                <h3 className="text-3xl font-bold mb-4">Gestionar Usuarios</h3>
                <p className="text-teal-100 text-lg leading-relaxed mb-6">
                  Gestiona los usuarios registrados en el sistema
                </p>
                <div className="inline-flex items-center text-white/90 group-hover:text-white transition-colors bg-white/10 px-6 py-3 rounded-full group-hover:bg-white/20">
                  <span className="mr-3 font-medium">Gestionar usuarios</span>
                  <svg
                    className="w-5 h-5 transform group-hover:translate-x-2 transition-transform duration-300"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </div>
              </CardContent>
            </Card>
          </Link>
        </div>

        {/* Botón Cerrar sesión */}
        <div className="flex justify-center">
          <Button
            variant="outline"
            size="lg"
            className="bg-white/90 backdrop-blur-sm border-gray-200 hover:bg-red-50 hover:border-red-300 hover:text-red-600 transition-all duration-300 px-10 py-4 text-lg shadow-lg hover:shadow-xl group"
            onClick={handleLogout} // Llamamos a la función cuando el botón es presionado
          >
            <LogOut className="w-6 h-6 mr-3 group-hover:rotate-12 transition-transform duration-300" />
            <span className="font-medium">Cerrar sesión</span>
          </Button>
        </div>
      </div>

      {/* Fondo decorativo */}
      <div className="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-blue-100/50 to-transparent"></div>
    </div>
  );
};
